$breakpoint-mobile: 768px;



.slide-container {
	position: relative;
	overflow: hidden;
	width: 100vw;
	height: 100vh;

	@media (max-width: $breakpoint-mobile) {
		height: auto;
		padding-bottom: 100px;
	}

	.slide{
		overflow: hidden;
		width: 100vw;
		height: 100vh;
		// transition: .5s;
		
		// default (the not active ones)
		position: absolute;
		left: 100%;
		top: 0;

		&.active {
			position: relative;
			left: 0;
			top: 0;
		}

		@media (max-width: $breakpoint-mobile) {
			height: 60vh;
			position: relative;
			left: unset;
			top: unset;
		}
	}
}


.bg{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 1;
}
.pattern{
	background-image: url(../img/bg-tileable.png);
	background-repeat: repeat-x;
	background-size: contain;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 2;
	opacity: .5;
	animation: blink 1s infinite linear;
}

.letters-wrapper{
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 3;
	overflow: hidden;
	ul{
		font-family: Helvetica, sans-serif;
		font-size: 33vh;
		text-transform: uppercase;
		font-weight: 700;
		display: flex;
		flex-direction: row;
		justify-content: center;
		@media (max-width: $breakpoint-mobile) {
			font-size: 18vh;
		}
		li{
			list-style: none;
			color: #e96f63;
			text-shadow: none;
			animation: animate 1s infinite linear;
			&:nth-child(1){
				animation-delay: .2s;
			}
			&:nth-child(2){
				animation-delay: .4s;
			}
			&:nth-child(3){
				animation-delay: .6s;
			}
			&:nth-child(4){
				animation-delay: .8s;
			}
			&:nth-child(5){
				animation-delay: 1s;
			}
			&:nth-child(6){
				animation-delay: 1.2s;
			}
			&:nth-child(7){
				animation-delay: 1.4s;
			}
			&:nth-child(8){
				animation-delay: 1.6s;
			}
			&:nth-child(9){
				animation-delay: 1.8s;
			}
			&:nth-child(10){
				animation-delay: 2s;
			}
			&:nth-child(11){
				animation-delay: 2.2s;
			}
			&:nth-child(12){
				animation-delay: 2.4s;
			}
			&:nth-child(13){
				animation-delay: .1s;
			}
			&:nth-child(14){
				animation-delay: .3s;
			}
			&:nth-child(15){
				animation-delay: .5s;
			}
			&:nth-child(16){
				animation-delay: .7s;
			}
			&:nth-child(17){
				animation-delay: .9s;
			}
			&:nth-child(18){
				animation-delay: 1.1s;
			}
			&:nth-child(19){
				animation-delay: 1.3s;
			}
			&:nth-child(20){
				animation-delay: 1.5s;
			}
			&:nth-child(21){
				animation-delay: 1.7s;
			}
			&:nth-child(22){
				animation-delay: 1.9s;
			}
			&:nth-child(23){
				animation-delay: 2.1s;
			}
			&:nth-child(24){
				animation-delay: 2.3s;
			}
		}
	}

	.flip{
		display: flex;
		justify-content: flex-end;
		transform:rotate(-180deg);
	}
}


.slide1{
	
	.bg-slide1{
		background-color: #faf09a;
	}
	.person{
		position: absolute;
		right: 50%;
		bottom: 0;
		transform: translate(-50% -50%);
		height: 95%;
		z-index: 4;
		@media (max-width: $breakpoint-mobile) {
			transform: translateX(50%);
			height: 80%;
		}
	}
}




.slide2{
	
		.bg-slide2{
			background-color: #49efa3;
		}
		.pattern{
			filter:sepia(100%);
		}
		.person{
			position: absolute;
			right: 50%;
			bottom: -60px;
			height: 95%;
			z-index: 4;
			@media (max-width: $breakpoint-mobile) {
				transform: translateX(50%);
			}
		}


}

.slide3{
	
		.bg-slide3{
			background-color: #7eb1ce;
		}
		.pattern{
			filter:sepia(100%);
		}
		.person{
			position: absolute;
			z-index: 4;
		}
		.girlsitting1{
			left: 25%;
			bottom: 5%;
			width: 33%;
			@media (max-width: $breakpoint-mobile) {
				width: 75%;
				left: 50%;
				bottom: 20%;
			}
		}
		.girlsitting2{
			left: 0%;
			bottom: 15%;
			width: 35%;
			@media (max-width: $breakpoint-mobile) {
				bottom: 30%;
				width: 75%;
			}
		}


}


.info{
	position: absolute;
	bottom: 15%;
	right: 10%;
	z-index: 6;
	display: flex;
	flex-direction: row;
	align-items: center;
	@media (max-width: $breakpoint-mobile) {
		right: 50%;
		bottom: 0;
		transform: translateX(50%);
	}
	img{
		width: 32px;
		height: 32px;

	}
	.whitebox{
		display: flex;
		justify-content: center;
		align-items: flex-start;
		flex-direction: column;
		background-color: #fff;
		color: #000;
		text-transform: uppercase;
		padding: 30px;
		margin: 30px;
		font-weight: bold;
		width: 350px;
		white-space: nowrap;
		box-shadow: 15px 15px 0px 0px rgba(0,0,0,0.75);
		text-decoration: none;

		&:hover{
			// background-color: #000;
			// color:#fff;

			// transform: scale(1);

		}
		@media (max-width: $breakpoint-mobile) {
			z-index: 999999;
			width: 100%;
		}
		p{
			font-size: 1.5rem;
		}
		span{
			font-size: 2.5rem;
		}
		.blackbox{
			background-color: #000;
			color: #d2fde9;
			text-transform: uppercase;
			padding: 10px;
			font-weight: bold;
			white-space: nowrap;
			transform: rotate(-18deg);
			position: absolute;
			top: 0;
			left: 0;

			&:hover{
				color: #000;
				background-color: #d2fde9;
				border: 1px solid #ddd;
				transform: rotate(-10deg);
			}
		}
	}
}


.mobile-blackbox{
	background-color: #000;
	width: 100%;
	height: 100px;
	bottom: 0;
	right: 50%;
	transform: translateX(50%);
	position: absolute;
	z-index: 5;
}



@keyframes animate{
	0%{
		color: #e96f63;
		text-shadow: none;
	}
	18%{
		color: #e96f63;
		text-shadow: none;
	}
	20%{
		color: #e96e63;
		text-shadow: 0 0 7px #e96e63, 0 0 14px #e6594d;
	}
	30%{
		color: #e96f63;
		text-shadow: none;
	}
	35%{
		color: #e96e63;
		text-shadow: 0 0 7px #e96e63, 0 0 14px #e6594d;
	}
	70%{
		color: #e96f63;
		text-shadow: none;
	}
	85%{
		color: #e96e63;
		text-shadow: 0 0 7px #e96e63, 0 0 14px #e6594d;
	}
	90%{
		color: #e96f63;
		text-shadow: none;
	}
	100%{
		color: #e96f63;
		text-shadow: none;
	}
}

@keyframes blink{
	0%{
		filter:sepia(100%);

	}
	18%{
		filter:sepia(100%);

	}
	20%{
		filter:sepia(50%);

	}
	30%{
		filter:sepia(100%);

	}
	35%{
		filter:sepia(0%);
	}
	70%{
		filter:sepia(100%);

	}
	85%{
		filter:sepia(50%);
	}
	90%{
		filter:sepia(100%);

	}
	100%{
		filter:sepia(100%);
	}
}


